.workCircle{
  position: absolute;
  z-index:2;
  width:calc(100% - 40px);
  margin:0 20px;
  overflow:hidden;
  // opacity:0;
  // transform:scale(0.8);
  // transition:1s;

  &.isWork{
    opacity:1;
    transform:scale(1);
  }
  .box{
    position: absolute;
    z-index:-1;
    left:0;
    top:0;
    width:100%;
    height:100%;
    background-color:#f5f1f0;
    background: radial-gradient(circle, rgba(250,247,247,1) 0%, rgba(245,241,240,1) 20%, rgba(232,221,219,1) 100%);
  }

  .list{
    position: relative;
    left:50%;
    width:100%;
    min-height:700px;
    padding-top:50px;
    //transition:1s;
    margin-left:-340px;
    //transform:translateX(-340px);

    h3{
      position: absolute;
      left: -80px;
      font-family: 'Abril Fatface';
      font-size: 104px;
      color: #ec493f;
      
      i{
        position: absolute;
        z-index:3;
        left: 145px;
        width: 600px;
        height: 307px;
        overflow:hidden;
        border-radius:50%;
        &:after{
          content:'Work';
          color:#fff;
          position: absolute;
          left: -145px;
        }
      }

    }

    .info{
      position: absolute;
      top: 560px;
      left: 140px;
      z-index: 4;
      color: #fff;
      opacity: 0.6;
      font-weight: normal;
    }
    
    .circle{
      position: absolute;
      left:0;
      z-index:2;
      top:50px;
      width: 635px;
      height: 635px;
      background-color: #ec493f;
      border-radius:50%;
      border-top: 15px solid #00838F;
      animation: rotateCircle 8s infinite  linear;
      box-shadow: inset -19px -30px 63px 16px rgba(255,81,0,1);
    }
    .circle::before {
      content: "";
      position: absolute;
      top: 0;
      left: 5px;
      width: 100%;
      height: 100%;
      transform: rotate(120deg);
      box-sizing: border-box;
      border-radius: 50%;
      border-top: 15px solid #F9A825;
    }
    
    @keyframes rotateCircle {
      0% {transform: rotate(0);}
      100% {transform: rotate(360deg);}
    }

    .circleBack{
      position: absolute;
      left: 23px;
      top: 101px;
      width: 619px;
      height: 600px;
      background-color: #eae0de;
      border-radius: 50%;
    }
    

    ul{
      position: relative;
      z-index:3;
      width:10000px;
      height:860px;
      overflow: hidden;
      transition:1s;
    }
    li{
      position: relative;
      z-index:2;
      float:left;
      width:100%;
      max-width:657px;
      width:100%;

      &::before{
        content:'';
        position: absolute;
        left:0;
        top:0;
        z-index:2;
        width:100%;
        height:100%;
      }
    }
    li a{
      position: relative;
      display:block;
      height:635px;
      transform:scale(0.6);
      transition:1.6s;
      transform-origin: center;
      figure{
        position: absolute;
        top:480px;
        right:230px;
        z-index:5;
        transition:0.9s ease;
        transform:translate(0, 0);

        > div{
          position: relative;
          width:304px;
          height:304px;
          overflow: hidden;
          border-radius:50%;
          border:12px solid transparent;
        }

        img{
          position: absolute;
          left:50%;
          top:50%;
          height:100%;
          transform: translate(-50%, -50%);
          transition:1s;
          filter: grayscale(1);
        }
      }
      dl{
        position: absolute;
        left:120px;
        top:190px;
        width:100%;
        padding:15px 20px;

        dt{
          padding-bottom:10px;
          color:#fff;
          font-size:20px;
          transform: translateX(200px);
          opacity:0;
          transition:1s;
          i{
            display:block;
            font-size: 58px;
            font-family: 'Cinzel Decorative', cursive;
            font-weight: 900;
            color: #fff;
            transform: translateX(-200px);
            opacity:0;
            transition:1s;
          }
        }
        .text{
          padding-left:20px;
          line-height:1.7;
          margin-bottom:0;
          color:#fff;
          transform: translateX(200px);
          opacity:0;
          transition:1.4s;
          span{
            display:block;
          }
        }
        .tools{
          display:block;
          padding:15px 0 0 31px;
          font-family: verdana;
          font-size: 11px;
          color: #fe9472;
          opacity:0;
          transition:1.4s;
        }
      }

    }
    .copy{
      position: absolute;
      left: 460px;
      top: 640px;
      width:130%;
      font-size: 50px;
      opacity: 0;
      transition: 1s;
      font-family: 'Cinzel Decorative', cursive;
    }
    li.active{
      &::before{
        display:none;
      }
      a{
        transform:scale(1);

        figure{
          transform: translate(190px, -450px);
          > div{
            border:12px solid #f5f1f03b;
          }
          img{
            filter:initial;
          }
        }

        dl{
          dt{
            transform: translateX(0);
            opacity:1;
            transition:1s;
            i{
              transform: translateX(-50px);
              opacity:1;
              transition:1s;
              font-size:78px;
            }
          }
          .text{
            transform: translateX(0);
            opacity:1;
            transition:1.4s;
          }
          .tools{
            opacity:1;
            transition-delay:0.7s;
          }
        }
      }
      .copy{
        opacity:0.2;
        left: 260px;
        transition:3s;
      }
    }
  }
}